@use "global" as *;

.blog-cards {
    display: grid;
    gap: rem(40);
    @include mq(md) {
        grid-template-columns: repeat(3, 1fr);
    }
}

.blog-cards--1col {
    grid-template-columns: repeat(1, 1fr);
    gap: rem(24);
    @include mq(md) {
        grid-template-columns: repeat(1, 1fr);
    }
}

.blog-cards--2col {
    @include mq(md) {
        grid-template-columns: repeat(2, 1fr);
        gap: rem(80) rem(40);
    }
}

