@use "global" as *;

.hamburger {
    position: relative;
    width: rem(40);
    height: rem(24);
    z-index: 100;
}

.hamburger span {
    position: absolute;
    width: rem(32);
    height: rem(2);
    background-color: $white;
    transition: 0.5s;
}

.hamburger span:nth-child(1) {
    top: rem(-1);
    right: 0;
}

.hamburger span:nth-child(2) {
    top: rem(11);
    right: 0;
    opacity: 1;
}

.hamburger span:nth-child(3) {
    top: rem(23);
    right: 0;
}

.hamburger.is-active span {
    width: rem(40);
    right: 0;
}

.hamburger.is-active span:nth-child(1) {
    top: rem(12);
    transform: rotate(35deg);
}

.hamburger.is-active span:nth-child(2) {
    top: rem(16);
    opacity: 0;
}

.hamburger.is-active span:nth-child(3) {
    top: rem(12);
    transform: rotate(-35deg);
}