@use "global" as *;

.mv {
    width: 100%;
    height: 100vh;
    min-height: rem(400);
}

.mv__inner {
    height: inherit;
    position: relative; // titleとswiper
    top: 0;
}

.mv__title {
    width: 100%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 2;
    @include mq(md) {
        left: 50.5%;
        top: 50.5%;
        transform: translate(-50.5%, -50.5%);
    }
}

.mv__swiper {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: inherit;
    z-index: -1;
}
