@use "global" as *;

.aside {
    width: 100%;
    @include mq(md) {
        max-width: rem(294);
        min-width: 192px;
        flex-shrink: 0;
    }
}

.aside__inner {
    padding-right: rem(25);
    padding-left: rem(25);
    @include mq(md) {
        padding-right: 0;
        padding-left: 0;
    }
}
