@use "global" as *;

.price__content {
    margin-top: rem(27);
    display: flex;
    flex-direction: column;
    gap: rem(40);
    @include mq(md) {
        margin-top: rem(40);
        flex-direction: row-reverse;
        justify-content: space-between;
        gap: calc(rem(40) / rem(1080) * 100%);
    }
}

.price__img {
    aspect-ratio: 345 / 227;
    position: relative;
    @include mq(md) {
        aspect-ratio: 492 / 746;
        width: 45.6824513%;
        flex-shrink: 0;
    }
}

.price__img source {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.price__img img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.price__img .is-color {
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
    background-color: $main-bg-color;
}

.price__lists {
    display: flex;
    flex-direction: column;
    gap: rem(39);
    @include mq(md) {
        width: 45.6824513%;
        text-align: left;
    }
}

.price__btn {
    margin-top: rem(40);
    text-align: center;
    @include mq(md) {
        margin-top: rem(77);
    }
}
