@use "global" as *;

.gallery-area {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: rem(8);
    @include mq(md) {
        grid-template-columns: 6.15fr 1fr 6.15fr;
        grid-auto-flow: dense;
    }
}

.gallery-area + .gallery-area {
    margin-top: rem(8);
}

.gallery-area__img:hover {
    cursor: pointer;
    opacity: 0.8;
}

.gallery-area__img:nth-of-type(6n + 1) {
    @include mq(md) {
        grid-column: span 1;
        grid-row: span 2;
    }
}

.gallery-area__img:nth-of-type(6n + 2) {
    @include mq(md) {
        grid-column: span 2;
        grid-row: span 1;
    }
}

.gallery-area__img:nth-of-type(6n + 3) {
    @include mq(md) {
        grid-column: span 2;
        grid-row: span 1;
    }
}
.gallery-area__img:nth-of-type(6n + 4) {
    @include mq(md) {
        grid-column: span 2;
        grid-row: span 1;
    }
}
.gallery-area__img:nth-of-type(6n + 5) {
    @include mq(md) {
        grid-column: span 2;
        grid-row: span 1;
    }
}
.gallery-area__img:nth-of-type(6n + 6) {
    @include mq(md) {
        grid-column: span 1;
        grid-row: span 2;
    }
}

.gallery-area__img:nth-of-type(6n + 1) img,
.gallery-area__img:nth-of-type(6n + 6) img {
    aspect-ratio: 492 / 746;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.gallery-area__img:nth-of-type(6n + 2) img,
.gallery-area__img:nth-of-type(6n + 3) img,
.gallery-area__img:nth-of-type(6n + 4) img,
.gallery-area__img:nth-of-type(6n + 5) img {
    aspect-ratio: 580 / 369;
    width: 100%;
    height: 100%;
    object-fit: cover;
}
