@use "global" as *;

.page-concept-modal {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    max-width: 100%!important;
    height: 100%;
    background: rgba(32, 32, 32, 0.80);
    z-index: 4000;
    cursor: pointer;
}

.page-concept-modal img {
    margin: auto;
    width: 100%;
    height: 100%;
    max-width: 90%;
    max-height: 90%;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    object-fit: contain;
}